html, body {
    height: 100vh;
    width: 100vw;
}

.calendar-screen {
    display: flex;
    flex-flow: column;
    height: 100vh;
}

.rbc-calendar {
    height: 100%;
}

.rbc-event{
    background-color: transparent;
}


/* Modal */

.ReactModalPortal>div {
    opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
    align-items: center;
    display: flex;
    justify-content: center;
    transition: opacity .2s ease-in-out;
    z-index: 999;
}

.modal-fondo {
    background-color: rgba(0, 0, 0, 0.3);
    bottom: 0;
    left: 0;
    right: 0;
    top: 0;
    position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
    opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
    opacity: 0;
}

.modal {
    background: white;
    border-radius: 5px;
    color: rgb(51, 51, 51);
    display: inline;
    max-height: 75%;
    max-width: 500px;
    outline: none;
    padding: 10px;
    overflow-y: auto;
    /* position: relative; */
}

div.fade.in.modal {
    display:flex !important;
  }
  
  .modal-dialog {
    margin: auto;
  }

.btnGuardar {
    /* position: relative; */
    width: 98%;
    bottom: 1%;
    right: 0.5rem;
}

/* ERROR CONCEPTO */

.errorConcepto {
    border: 2px solid red;
    border-radius: 4px;
}

/* Datepicker */

.react-datetime-picker input:focus {
    outline: none;
}

.react-datetime-picker__wrapper {
    border: 0px solid gray !important;
}


/* RESUMEN */

.resumen-span{
    color: black !important;
    font-weight: bold !important;
}

@media only screen and (max-width: 992px) {
    .resumen-mobile{
        display: block;
    }
    .resumen-sticky{
        display: none;
        margin-left: auto;
    }
}
@media only screen and (min-width: 992px) {
    .resumen-mobile{
        display: none;
    }
    .resumen-sticky{
        display: block;
        margin-left: 20vw;
    }
}



/* FABS */

.fab {
    border-radius: 100%;
    bottom: 25px;
    font-size: 30px;
    padding: 25px;
    position: fixed;
    right: 25px;
}

.fab-danger {
    bottom: 25px;
    padding: 10px;
    position: fixed;
    left: 25px;
}

.nav-link {
    cursor: pointer;
}

nav-item .disabled{
    color: red;
}

.tableEmpty{
    color: red;
    text-align: center;
    
}


.verde{
    background-color: greenyellow;
}

.rojo{
    background-color: crimson;
}

.table td{
    border-top: 0px;
}